#header {
  @include pos(ab, 0 0 null);
  width: 100%;
  z-index: 100;
  padding: em(25) 0;

  @include media('<='+em(1200)) {
    padding: em(7) 0;
  }

  .headerGrid {
    display: grid;
    grid-template-columns: auto 1fr;
    grid-template-rows: auto;
    grid-template-areas: "brand topbar""brand navbar";
    padding: 0 em(35);

    @include media('<='+em(1600)) {
      padding: 0;
    }

    @include media('<='+em(1500)) {
      grid-template-areas: "brand topbar""navbar navbar";
      align-items: center;
    }
    @include media('<='+em(1199)) {
      grid-template-columns: auto 1fr auto;
      grid-template-areas: "brand topbar navButton";
    }
    @include media('<='+em(1000)) {
      grid-template-columns: 1fr auto;
      grid-template-areas: "topbar topbar" "brand navButton";
    }
    @include media('<='+em(767)) {
      grid-template-columns: 1fr auto;
      grid-template-areas: "brand navButton";
    }
  }

  .brand {
    grid-area: brand;

    a {
      display: $b;
      padding: em(20) em(20) em(5) 0;

      @include media('<='+em(1750)) {
        max-width: em(300);
      }

      @include media('<='+em(1500)) {
        max-width: em(250);
        padding: 0 em(20) 0 0;
      }
    }
  }
  .navToggle{
    padding-left: em(15);
    a{
      width: em(25);
      text-align: right;
      float: right;
      span{
        height: 2px;
        display: $b;
        width: em(25);
        float: right;
        background-color: $white;
        margin: em(4) 0;
        @include radius(3px);
        &:nth-child(2){
          width: em(20);
        }
      }
    }
  }

  .topBar {
    text-align: right;
    color: $white;
    grid-area: topbar;

    .blk {
      display: $ib;
      vertical-align: middle;
      margin: 0 em(50);
      @include media('<='+em(1199)) {
        margin: 0 em(10);
      }

      &:first-child {
        margin-left: 0;
      }

      &:last-child {
        margin-right: 0;
        margin-left: 0;
      }

      span {
        color: $theme-warning;
      }
      a{
        color: inherit;
        &:hover{
          color: $theme-primary;
        }
      }

      .button {
        white-space: nowrap;
        text-transform: uppercase;
        &:hover{
          color: $white;
        }
        @include media('<='+em(1199)) {
          font-size: rem(13);
          padding: 0 em(8);
          height: 35px;
          line-height: 33px;
        }
      }
    }
  }

  nav {
    @include media('>='+em(1200)) {
      grid-area: navbar;
    }
    @include transition(all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1));
    @include media('<='+em(1199)) {
      height: 100vh;
      overflow: auto;
      background-color: $theme-default;
      z-index: 101;
      padding: em(30) 0;
      visibility: hidden;
      @include translateX(100%);
      @include opacity(0);
      @include pos(f, 0);
    }

    .mobLogo{
      @include pos(ab, 10px null null 10px);
      max-width: 80px;
      display: $ib;
    }

    .navClose{
      @include pos(ab, 10px 10px null null);
      @include square(em(30));
      &:after, &:before{
        content: "";
        @include pos(ab, 0);
        margin: auto auto;
        background-color: $white;
        @include rotate(45deg);
      }
      &:after{
        height: 2px;
        width: 100%;
      }
      &:before{
        height: 100%;
        width: 2px;
      }
    }

    ul {
      float: right;

      @include media('<='+em(1500)) {
        margin-top: em(15);
        float: none;
        text-align: center;
      }

      li {
        display: $ib;
        padding: 0 em(14);

        @include media('<='+em(1700)) {
          padding: 0 em(10);
        }
        @include media('<='+em(1199)) {
          display: $b;
          padding: em(10) 0;
        }

        &:first-child {
          padding-left: 0;
        }

        &:last-child {
          padding-right: 0;
        }

        a {
          display: $b;
          color: $white;
          white-space: nowrap;
          font-size: em(18);
          @include media('<='+em(1199)) {
            display: $ib;
          }

          &:hover {
            color: $theme;
          }
        }
      }
    }
  }
}

body{
  &.menuOpen{
    overflow: hidden;
    #header{
      nav{
        visibility: visible;
        @include translateX(0);
        @include opacity(1);
      }
    }
  }
}