#hero {
  @include pos(r, null);

  &:after,
  &:before {
    content: "";
    @include pos(ab, null 0 0);
    width: 50%;
    height: em(65);
    background-color: $white;
    z-index: 1;

    @include media('<='+em(767)) {
      height: em(30);
    }
  }

  &:after {
    right: auto;
    @include transform-origin(0, 100%);
    @include transform(skew(55deg));
  }

  &:before {
    left: auto;
    @include transform-origin(100%, 100%);
    @include transform(skew(-55deg));
  }

  .floatingSocial {
    @include pos(ab, 50% em(60) null null);
    @include translateY(-50%);

    @include media('<='+em(991)) {
      right: em(10);
    }

    a {
      font-size: em(24);
      color: $white;
      display: $b;
      padding: em(8) em(4);
      @include pos(r, null);

      &+a {
        &:before {
          content: "";
          display: $b;
          height: 1px;
          background-color: lighten($black, 45%);
          @include pos(ab, 0 0 null);
          @include rotate(-20deg);
        }
      }

      &:hover {
        color: $theme;
      }
    }
  }

  .carousel-inner {
    .carousel-item {
      background: no-repeat center center scroll;
      -webkit-background-size: cover;
      -moz-background-size: cover;
      -o-background-size: cover;
      background-size: cover;


      &:before {
        content: "";
        @include pos(ab, 0);
        @include rgba($black, 0.7);
      }

      .carouselInner {
        @include pos(r, null);
        @include experimental-value(min-height, calc(100vh));
        @include display-flex;
        align-items: center;
        justify-content: center;
        min-height: 100vh;
        padding: em(180) 0;

        @include media('<='+em(767)) {
          padding: em(100) 0;
        }
      }

      .carousel-caption {

        @include pos(r, null 0 0);
        @include flex-basis(em(650));
        padding: em(20);
        color: $white;

        h2 {
          font-size: em(90);
          font-family: $font-alt;
          color: $theme;
          text-transform: uppercase;
          font-weight: 700;
          line-height: 1;
          @include margin-bottom(0);

          @include media('<='+em(991)) {
            font-size: em(70);
          }

          @include media('<='+em(575)) {
            font-size: em(50);
          }
        }

        h4 {
          font-size: em(36);
          font-family: $font-alt;
          @include pos(r, null);
          @include margin-bottom(0.5);

          @include media('<='+em(991)) {
            font-size: em(30);
          }

          @include media('<='+em(575)) {
            font-size: em(24);
          }

          &:after {
            content: "";
            display: block;
            height: 1px;
            width: 295px;
            margin: 0 auto;
            @include margin-top(0.5);
            @include rgba($white, 0.2);
          }
        }

        p {
          font-size: em(19);

          @include media('<='+em(575)) {
            font-size: em(16);
          }
        }

        a {
          display: $ib;
          color: $white;
          border: 1px solid $white;
          font-size: em(30);
          text-transform: uppercase;
          // min-width: rem(350);
          min-width: em(190);
          z-index: 1;
          overflow: hidden;
          @include padding(0.5);
          @include radius(0.3em);
          @include pos(r, null);

          &:before {
            content: '';
            background-color: $theme;
            z-index: -2;
            @include transition(all 0.5s ease);
            @include pos(ab, 0 50%);
            @include opacity(0);
          }

          &:hover {
            border-color: $theme;

            &:before {
              left: 0;
              right: 0;
              @include opacity(1);
            }
          }

          @include media('<='+em(991)) {
            font-size: em(24);
          }

          @include media('<='+em(575)) {
            font-size: em(20);
          }
        }
      }
    }
  }

  .carousel-indicators {
    @include media('>='+em(768)) {
      bottom: em(160);
    }

    @include media('<='+em(767)) {
      bottom: em(50);
    }

    li {
      @include radius(0.2em);
      height: em(7);
      border: $n;
      &:hover{
        background-color: $theme-primary;
      }
      &.active{
        &:hover{
          background-color: $theme;
        }
      }
    }
  }
}

#popularCourseWrapper {
  padding: em(65) 0 em(100);

  @include media('<='+em(991)) {
    padding: em(50) 0 em(30);
  }

  .container {
    &>.row {
      &>[class^="col-"] {
        @include from-end(2) {
          &:before {
            content: '';
            @include pos(ab, 0 null 0 0);
            margin: $auto;
            height: 80%;
            width: 1px;
            background-color: darken($white, 10%);

            @include media('<='+em(991)) {
              height: 1px;
              width: 100%;
              left: 50%;
              bottom: auto;
            }

            @include media('<='+em(575)) {
              display: $n;
            }
          }
        }
      }
    }
  }

  .courseBlock {
    text-align: center;
    padding: em(65) em(20);
    margin: em(15) 0;
    display: $b;
    color: $theme-default;
    overflow: hidden;
    @include radius(0.5rem);
    @include pos(r, null);

    @include media('<='+em(575)) {
      padding: em(30) em(15);
    }

    &:before {
      content: "";
      z-index: 0;
      @include pos(ab, 0);
      @include scale(0);
      @include radius(50%);
      @include background-image(linear-gradient(to right, $theme-primary, lighten($theme-primary, 5%)));
      @include transition(all 0.5s);
      @include opacity(0);
    }

    &:hover {
      // background-color: $theme-primary;
      // @include background-image(linear-gradient(to right, $theme-primary, lighten($theme-primary, 5%)));
      color: $white;
      @include transition(box-shadow 0.4s linear 0.2s);
      @include box-shadow(0 0 15px rgba($theme-primary, 0.4));

      &:before {
        @include scale(1.2);
        @include radius(0.5em);
        @include opacity(1);
      }
    }

    & * {
      position: relative;
      z-index: 1;
    }

    // @include display-flex;
    // @include flex-direction(column);
    i {
      height: 1em;
      font-size: em(75);
    }

    h5 {
      font-size: em(22);
      font-weight: 700;
      margin: em(10) 0 0;
    }
  }
}


#homeWrapper3 {
  @include pos(r, null);
  background-color: lighten($theme-default, 10%);
  padding: em(125) 0 em(100);
  color: $white;

  @include media('<='+em(991)) {
    padding: em(75) 0;
  }

  @include media('<='+em(767)) {
    padding: em(50) 0;
  }

  h2 {
    color: $theme;
    font-size: em(93);
    font-weight: 400;
    line-height: 1;

    @include media('<='+em(767)) {
      font-size: em(60);
      margin-bottom: em(10);
    }

    span {
      display: $b;
      color: $white;
      font-size: em(9);
    }
  }

  .content {
    max-height: em(400);
    padding-left: em(25);

    @include media('<='+em(767)) {
      padding-left: 0;
    }

    &.mCS-dir-rtl {
      &>.mCSB_inside {
        &>.mCSB_container {
          margin-left: em(65);

          @include media('<='+em(767)) {
            margin-left: em(40);
          }
        }
      }
    }

    &.mCustomScrollbar {
      .mCSB_container {
        padding: 0 0 em(15) 0;
      }
    }

    .mCSB_scrollTools {
      @include width(28);
      @include opacity(1);
      bottom: 15px;

      .mCSB_draggerRail {
        background-color: darken($white, 3%);
      }

      .mCSB_dragger {
        .mCSB_dragger_bar {
          @include square(em(28));
          background-color: $white;
          border: 1px solid $theme-primary;
        }
      }
    }

    h3 {
      font-size: em(34);
      margin-bottom: em(16);

      @include media('<='+em(767)) {
        font-size: em(24);
      }
    }

    p {
      font-size: em(22);

      @include media('<='+em(767)) {
        font-size: em(18);
      }
    }
  }
}

#homeVideoWrapper {
  padding: em(80) 0 em(105);

  @include media('<='+em(991)) {
    padding: em(75) 0;
  }

  @include media('<='+em(767)) {
    padding: em(50) 0;
  }

  .videoGridTemplate {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 30px;

    @include media('<='+em(900)) {
      grid-template-columns: 1fr 1fr;
    }

    @include media('<='+em(600)) {
      grid-template-columns: 1fr;
    }

    .videoBlock {
      background-color: darken($white, 10%);
      height: em(265);
      overflow: hidden;
      margin: 0;
      @include pos(r, null);

      &:hover {
        .overlay {
          @include translateY(0);
          @include opacity(1);
        }

        figcaption {
          @include translateY(100%);
          @include opacity(0);
        }
      }

      &:first-child {
        grid-row: 1 / 3;
        @include height-auto;

        @include media('<='+em(900)) {
          grid-row: 1;
        }
      }

      &:nth-child(2) {
        grid-column: 2 / 4;

        @include media('<='+em(900)) {
          grid-column: 2;
        }

        @include media('<='+em(600)) {
          grid-column: 1;
        }
      }

      figcaption {
        font-size: em(18);
        padding: em(8) em(14);
        text-transform: uppercase;
        @include rgba($white, 0.7);
        @include pos(ab, null 0 0);
        @include transition(all 0.6s);

        @include media('<='+em(767)) {
          font-size: em(14);
        }
      }

      video {
        @include square(100%);
        object-fit: cover;
      }

      .overlay {
        z-index: 1;
        overflow: hidden;
        @include transition(all 0.4s);
        @include translateY(-100%);
        @include opacity(0);
        @include pos(ab, 0);
        @include rgba($black, 0.3);

        button {
          background-color: transparent;
          padding: 0;
          font-size: em(35);
          color: $theme;
          border: $n;
          margin: $auto;
          line-height: 1;
          outline: $n;
          border: 2px solid $theme;
          @include radius(50%);
          @include square(em(40));
          @include pos(ab, 0);

          .icon-play {
            @include pos(r, null null null 4px);
          }

          &.playButton {}

          &.pauseButton {}
        }
      }
    }
  }
}

#newsLetterWrapper {
  background-color: $theme-primary;
  padding: em(100) 0 em(90);

  @include media('<='+em(991)) {
    padding: em(75) 0;
  }

  @include media('<='+em(767)) {
    padding: em(40) 0;
  }

  h2 {
    color: $theme;
    font-size: em(93);
    font-weight: 400;
    line-height: 1;

    @include media('<='+em(992)) {
      font-size: em(60);
      margin-bottom: em(10);
    }

    @include media('<='+em(767)) {
      font-size: em(40);
    }

    span {
      display: $b;
      color: $white;
      font-size: em(9);
    }
  }

  .formWrap {
    @include media('>='+em(992)) {
      padding-left: em(25);
    }

    .input-group {
      .form-control {
        font-size: em(22);
        @include height(55);

        @include media('<='+em(575)) {
          font-size: em(18);
          width: 100%;
          @include radius(em(5));
        }
      }

      .input-group-append {
        @include media('<='+em(575)) {
          width: 100%;
          margin-top: em(16);

        }
      }

      .button {
        background-color: $theme;
        font-size: em(32);
        height: auto;
        @include radius(0 5px 5px 0);

        &:before {
          background-color: darken($theme, 10%);
        }

        &:hover {
          border-color: darken($theme, 10%);
        }

        @include media('<='+em(767)) {
          font-size: em(24);
        }

        @include media('<='+em(575)) {
          width: 100%;
          @include radius(em(5));
        }

      }
    }
  }
}

#mapWrapper {
  #map_canvas {
    max-height: em(630);
    min-height: em(320);

    &:before {
      content: "";
      display: $b;
      padding-top: 62%;
    }

    .csMarkerBlock {
      background-color: $white;
      padding: em(30) em(40);
      display: $ib;
      @include radius(em(10));
      @include box-shadow(0 0 5px rgba($black, 0.2));
      @include translate(-50%, -100%);
      @include pos(r, null);

      &:before {
        @include arrow(bottom, $white, em(25), true);
      }

      &:hover {
        @include animation(bouncing 1s infinite);
      }
    }
  }
}

@include keyframes(bouncing) {
  0% {
    margin-top: 0;
  }

  50% {
    margin-top: -10px;
  }

  100% {
    margin-top: 0;
  }
}