#footer {
  border-top: 1px solid darken($white, 20%);
  padding-top: em(70);
  overflow: hidden;
  @include pos(r, null);

  @include media('<='+em(757)) {
    padding-top: em(50);
  }

  h3 {
    font-size: em(18);
    text-transform: uppercase;
    color: $theme-primary;
    margin-bottom: em(12);
    @include pos(r, null);
    @include media('<='+em(575)) {
      border: 1px solid darken($white, 10%);
      padding: em(7) em(12);
      @include radius(4px);
      &:after, &:before{
        content: "";
        background-color: $theme-primary;
        height: 17px;
        width: 1px;
        margin: $auto;
        @include transition(all 0.4s);
        @include pos(ab, 0 10px 0 null);
      }
      &:before{
        height: 1px;
        width: 17px;
      }
      &:after{
        right: 18px;
      }
    }
    &.open{
      &:after, &:before{
        @include rotate(-90deg);
      }
      &:before{
        @include opacity(0);
      }
    }
  }

  ul {
    margin-bottom: em(16);
    @include media('<='+em(575)) {
      display: $n;
      border: 1px solid darken($white, 10%);
      padding: em(7) em(12);
      @include radius(4px);
    }
    li {
      &+li {
        &:before {
          content: "";
          max-width: em(150);
          border-bottom: 1px solid darken($white, 10%);
          display: $b;
        }
      }

      a {
        color: lighten($theme-default, 15%);
        padding: em(10) 0;
        line-height: 1;
        display: $b;
        &:hover{
          color: $theme;
        }
      }
    }
  }
  ._social{
    a{
      display: $ib;
      vertical-align: middle;
      margin-right: em(10);
      color: $theme;
      &:hover{
        color: $theme-primary;
        @include translateY(-5px);
      }
    }
  }
  .blk{
    margin-top: em(15);
    h6{
      text-transform: uppercase;
      color: $theme-primary;
      margin-bottom: em(5);
    }
    p{
      margin: 0;
      a{
        color: inherit;
        &:hover{
          color: $theme;
        }
      }
    }
  }
  .copyRightBlock{
    background-color: lighten($black, 35.2%);
    padding: em(20) 0;
    color: $white;
    margin-top: em(45);
    p{
      margin: 0;
    }
  }
}