main {
  overflow: hidden;
}

.container {
  max-width: pxtoem(1170, $em-base);
}

// Buttons
.button {
  display: $ib;
  padding: 0 rem(20);
  font-size: rem(18);
  background-color: darken($white, 20%);
  color: $theme-default;
  height: 50px;
  line-height: 48px;
  border: 1px solid transparent;
  text-align: center;
  font-family: $font;
  z-index: 1;
  overflow: hidden;
  @include radius(0.3em);
  @include box-shadow-none;
  @include pos(r, null);

  &:before {
    content: '';
    background-color: darken($white, 30%);
    z-index: -2;
    @include transition(all 0.5s ease);
    @include pos(ab, 0 50%);
    @include opacity(0);
  }

  &:hover {
    background-color: darken($white, 30%);

    &:before {
      left: 0;
      right: 0;
      @include opacity(1);
    }
  }

  &.button-theme {
    background-color: $theme;
    color: $white;
    &:before {
      background-color: $theme-primary;
    }
    &:hover{
      border-color: $theme-primary;
    }
  }

  &.button-plain {
    background-color: $white;
    &:before {
      background-color: $theme;
    }
    &:hover{
      color: $white;
      border-color: $theme;
    }
  }

  &.button-border {
    background-color: transparent;
    border-color: darken($white, 20%);

    &.button-plain {
      border-color: $white;
      color: $white;
      &:before {
        background-color: $white;
      }
      &:hover{
        color: $theme-default;
      }
    }
  }

  &.button-upercase {
    text-transform: uppercase;
  }
}


// HEADING TITLE
.h2Title {
  font-size: em(44);
  font-weight: 400;
  margin-bottom: em(30);
  @include pos(r, null);

  @include media('<='+em(991)) {
    font-size: em(40);
    margin-bottom: em(20);
  }

  @include media('<='+em(767)) {
    font-size: em(32);
  }

  &:after {
    content: "";

    width: em(80);
    height: em(1.85);
    background-color: $theme;
    display: $b;
    margin: em(5) auto 0;
  }
}