@mixin font-face($font-name, $font-path, $font-weight, $font-style) {
   @font-face {
      font-family: $font-name;
      src: url("#{$font-path}.eot");
      src: url("#{$font-path}.eot?#iefix") format("embedded-opentype"), url("#{$font-path}.woff") format("woff"), url("#{$font-path}.ttf") format("truetype"), url("#{$font-path}.svg##{$font}") format("svg");
      font-weight: $font-weight;
      font-style: $font-style;
   }

   //Chrome for Windows rendering fix: http://www.adtrak.co.uk/blog/font-face-chrome-rendering/
   @media screen and (-webkit-min-device-pixel-ratio: 0) {
      @font-face {
         font-family: $font-name;
         src: url("#{$font-path}.svg##{$font}") format("svg");
      }
   }
}

@mixin DidactGothic {
   font-family: '$font-name', arial, sans-serif;
   font-weight: 400;
   font-style: normal;
}

@mixin CaviarDreams {
   font-family: '$font-name', arial, sans-serif;
   font-weight: 400;
   font-style: normal;
}

@mixin CaviarDreams {
   font-family: '$font-name', arial, sans-serif;
   font-weight: 700;
   font-style: normal;
}