//Fonts
@include font-face($font, "#{$font-path}/DidactGothic-Regular", 400, normal);
@include font-face($font-alt, "#{$font-path}/CaviarDreams", 400, normal);
@include font-face($font-alt, "#{$font-path}/CaviarDreams-Bold", 700, normal);

@font-face {
  font-family: 'icomoon';
  src: url('#{$iconFontPath}/icomoon.eot?g1qf1f');
  src: url('#{$iconFontPath}/icomoon.eot?g1qf1f#iefix') format('embedded-opentype'),
  url('#{$iconFontPath}/icomoon.ttf?g1qf1f') format('truetype'),
  url('#{$iconFontPath}/icomoon.woff?g1qf1f') format('woff'),
  url('#{$iconFontPath}/icomoon.svg?g1qf1f#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

