// THEME AND COLOR
$black : #000000 !default;
$white : #ffffff !default;
$theme-default : #1e1e1e !default;
$theme : #9dc731 !default;
$theme-primary: #3a89c1 !default;
$theme-warning: #e3a03c !default;
// PATH & FONT NAME
$font : 'DidactGothic' !default;
$font-alt : 'CaviarDreams' !default;
$imgpath : "../images/" !default;
$font-path : "../fonts" !default;
$iconFontPath : "../fonts/icons" !default;
$url : $imgpath !default;
// FONTS and TEXT STYLE
$fontnormal : 15px !default;
$fontsmall : 13px !default;
$lineheight : 1.7 !default;
$spacing : 5px !default;
// Display
$in : inline !default;
$ib : inline-block !default;
$b : block !default;
$t : table !default;
$cell : table-cell !default;
$n : none !default;
//TRANSITION EFFECT
$trasition_all : all 300ms ease-in-out !default;
// Margin
$a : auto !default;
$auto : auto auto !default;
$za : 0 auto !default;